axios.defaults.withCredentials = true
axios.defaults.timeout = 30000

Promise.prototype.done = function (fn) {
  this.then(resp => {
    if (resp.ret !== 1) {
      Toast({
        message: resp.data || resp.msg || '网络出现异常，请稍后再试',
        duration: 3000
      })
    }
    typeof fn == "function" && resp.ret == 1 && fn(resp);
  })
  return this;
}


