export const POSTJSON = (url, params) => {
  const options = {
    method: 'POST',
    headers: {
      'content-type': 'application/json'
    },
    data: params,
    url,
  }
  return axios(options).then(res => res.data)
}
export const POST = (url, params, toast = false, _JSON = 'form') => {
  const options = {
    method: 'POST',
    headers: {
      'content-type': _JSON == 'form' ? 'application/x-www-form-urlencoded;charset=UTF-8' : _JSON
    },
    data: _JSON == 'form' ? qs.stringify(params) : params,
    url,
  }
  if (toast) {
    Toast.loading({
      message: '加载中...',
      forbidClick: true,
      duration: 0
    });
  }
  return new Promise((resolve, reject) => {
    axios(options).then(res => {
      Toast.clear()
      resolve(res.data)
    })
  })
  // return axios(options).then(res => res.data)
}
export const POSTIMG = (url, data) => {
  const options = {
    method: 'POST',
    headers: {
      'content-type': 'multipart/form-data'
    },
    data,
    url,
  }
  return axios(options).then(res => res.data)
}

export const GET = (url, params, toast = false) => {
  const options = {
    method: 'GET',
    headers: {
      'content-type': 'application/x-www-form-urlencoded;charset=UTF-8'
    },
    params: params,
    url: url,
  }
  if (toast) {
    Toast.loading({
      message: '加载中...',
      forbidClick: true,
      duration: 0
    });
  }
  return new Promise((resolve, reject) => {
    axios(options).then(res => {
      Toast.clear()
      resolve(res.data)
    })

  })


  // return axios.get(`${url}`, { params: params }).then(res => res.data)
}

export const PUT = (url, params) => {
  return axios.put(`${url}`, params).then(res => res.data)
}

export const DELETE = (url, params) => {
  return axios.delete(`${url}`, {
    params: params
  }).then(res => res.data)
}

export const PATCH = (url, params) => {
  return axios.patch(`${url}`, params).then(res => res.data)
}



